header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::after {
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.me {
    background: linear-gradient(var(--color-primary), transparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: 37%;
    left: calc(50%-11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
}

@media screen and (max-width:1024) {
    header {
        height: 68vh;
    }
}

@media screen and (max-width:600px) {

    header {
        height: 100vh;
        width: 100%;
    }
 
  .header__socials,.scroll__down{
    display: none;
  }
}

@media screen and (max-width:820px) {

    header {
        height: 100vh;
    }
    header .header__container .me {
   position: absolute;
   left: 26%;
    }
 
}

@media screen and (max-width:412px) {
    header {
        height: 100vh;
    }
    header .header__container .me {
   position: absolute;
   background-color: aquamarine;
   left: 2%;
    }
 
  .header__socials,.scroll__down{
    display: none;
  }
}