.experience__conatiner{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
}
.experience__conatiner > div{
    background: #3f2d93;
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 6px solid transparent;
    transition: var(--transition);
}
@keyframes color{
    100%{
        filter: hue-rotate(360deg);
    }
}
.experience__conatiner >div:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor: pointer;
    animation: color 2s linear infinite;
}
.experience__conatiner > div h3{
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-primary);
}
.experience__content{
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
}
.experience__details{
    display: flex;
    gap: 1rem;
}
.icons{
    margin-top: 6px;
    color: var(--color-primary);
}
@media screen and (max-width:820px) {

    .experience__conatiner {
        gap: 1rem;
        grid-template-columns: 1fr;
     }
     .experience__conatiner >div{
        width: 100%;
        padding: 2rem 1rem;
     }
   
 
}